import { Component, OnInit } from '@angular/core';
import { ItemsService } from '../items.service';
import { Response } from '@angular/http';
import Item from '../item.model';

@Component({
  selector: 'app-container',
  templateUrl: './container.component.html',
  styleUrls: ['./container.component.css']
})
export class ContainerComponent implements OnInit {
  items: Item[] = [];
  public newItem: Item = new Item();
  itemName;
  public selected_item: Item = new Item();
  show_item_details:boolean = false;
  checked: boolean = false;
  

  constructor(private itemsService: ItemsService) { }

  ngOnInit() {
    this.getAllItems();
  }

  getAllItems() {
    this.itemsService.getItems().subscribe((data) => {
      this.items = data;     
    });
  }

  addItem(event) {   
    
    if(event.keyCode == 13) {
      this.newItem.name = this.itemName;
      this.newItem.quantity = null;
      this.newItem.price = null;
      this.newItem.description = '';
      this.newItem.marked = false;

      this.itemsService.addItem(this.newItem).subscribe((data) => {
        console.log(data);
        this.items.push(data);  
        this.itemName = '';
      });
    }
  }

  deleteItem(item) {
    this.itemsService.deleteItem(item.id).subscribe((data) => {
      console.log(data);
      this.items = this.items.filter(i => i.id !== item.id);         
    });
  }

  showItemDetails(item) {
    this.selected_item = item;
    this.show_item_details = true;
  }

  updateItem(event, quantity, price, description) {
    if(event.keyCode == 13) {
      this.selected_item.quantity = quantity;
      this.selected_item.price = price;
      this.selected_item.description = description;
  
      this.items = this.items.filter(i => i.id !== this.selected_item.id); 
         
      this.itemsService.updateItem(this.selected_item).subscribe((data) => {
        console.log(data);
        this.items.push(data); 
        this.showItemDetails(data);
      });
    }
      
  }

  markItem(item) {
    if(item.marked == true) {
      item.marked = false;  
    }else{
      item.marked = true;
    }

    this.items = this.items.filter(i => i.id !== item.id); 
        
    this.itemsService.markItem(item).subscribe((data) => {
      console.log(data);
      this.items.push(data);
    });
  }

}
